import { Component } from '@angular/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BulkDeviceRegistrationModalComponent } from './bulk-device-registration-modal.component';
import { ExtensibleDeviceRegistrationProvider } from '../extensible/base-device-registration.model';

@Component({
  selector: 'extensible-device-registration-component',
  templateUrl: 'bulk-device-registration-button.component.html'
})
export class BulkDeviceRegistrationButtonComponent {
  bulkProvidersMetaData: Promise<ExtensibleDeviceRegistrationProvider[]>;

  constructor(private modalService: BsModalService) {}

  click() {
    this.modalService.show(BulkDeviceRegistrationModalComponent, {
      class: 'modal-sm',
      ariaDescribedby: 'modal-body',
      ariaLabelledBy: 'modal-title',
      ignoreBackdropClick: true
    });
  }
}
