import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { C8yStepper, GainsightService, gettext } from '@c8y/ngx-components';
import { FormGroup } from '@angular/forms';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { ExtensibleDeviceRegistrationService } from './extensible-device-registration.service';
import { RegisterDeviceService } from '../../register-device.service';
import { FormlyFieldConfig } from '@ngx-formly/core';
import { STEP_STATE } from '@angular/cdk/stepper';
import { PRODUCT_EXPERIENCE } from '../base-device-registration.model';

@Component({
  selector: 'c8y-extensible-device-registration-stepper',
  templateUrl: 'extensible-device-registration-stepper.component.html'
})
export class ExtensibleDeviceRegistrationStepperComponent implements OnInit {
  message: string;
  errorMessage: string;
  pendingStatus = false;
  forms: FormGroup[] = [];
  model: any = {};
  @ViewChild(C8yStepper, { static: true })
  stepper: C8yStepper;

  @Input() contextPath: string;
  @Input() labels: string[];
  @Input() steps: FormlyFieldConfig[][];

  private readonly SERVER_ERROR = gettext('Cannot register your device.');
  private readonly SUCCESSFUL_REGISTRATION = gettext('Your device was successfully registered.');
  private readonly NO_CONNECTION = gettext('Unable to reach the microservice.');

  constructor(
    private customProviderService: ExtensibleDeviceRegistrationService,
    private registerDeviceService: RegisterDeviceService,
    private bsModalRef: BsModalRef,
    private gainsightService: GainsightService
  ) {}

  ngOnInit() {
    this.steps.forEach(() => this.forms.push(new FormGroup({})));
  }

  async save() {
    this.pendingStatus = true;
    this.errorMessage = null;
    const { res, data } = await this.customProviderService
      .registerDevice(this.contextPath, this.model)
      .catch(() => {
        return {
          res: undefined,
          data: undefined
        };
      })
      .finally(() => {
        this.pendingStatus = false;
        this.stepper.next();
      });

    if (res && data) {
      if (res.status >= 400 && res.status < 500) {
        this.handleError(data.message || this.SERVER_ERROR);
      } else if (res.status >= 500) {
        this.handleError(this.SERVER_ERROR);
      } else {
        this.gainsightService.triggerEvent(PRODUCT_EXPERIENCE.EVENT, {
          result: PRODUCT_EXPERIENCE.RESULT.SUCCESS,
          component: PRODUCT_EXPERIENCE.COMPONENT.EXTENSIBLE_SINGLE
        });
        this.message = this.SUCCESSFUL_REGISTRATION;
      }
    } else {
      this.handleError(this.NO_CONNECTION);
    }
  }

  close() {
    this.bsModalRef.hide();
  }

  complete() {
    this.registerDeviceService.list();
    this.bsModalRef.hide();
  }

  goToFirstStep() {
    this.stepper.selectedIndex = 0;
  }

  isRegistrationStep() {
    return this.stepper.selectedIndex === this.steps.length - 1;
  }

  finalStepStatus() {
    if (this.stepper.selectedIndex === this.steps.length) {
      if (this.errorMessage) {
        return STEP_STATE.ERROR;
      } else {
        return STEP_STATE.DONE;
      }
    } else {
      return STEP_STATE.NUMBER;
    }
  }

  private handleError(message: string) {
    this.gainsightService.triggerEvent(PRODUCT_EXPERIENCE.EVENT, {
      result: PRODUCT_EXPERIENCE.RESULT.FAILURE,
      component: PRODUCT_EXPERIENCE.COMPONENT.EXTENSIBLE_SINGLE
    });
    this.errorMessage = message;
  }
}
