import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import {
  DeviceRegistrationService,
  DeviceRegistrationStatus,
  IDeviceRegistration,
  IDeviceRegistrationAccept,
  IDeviceRegistrationCreate,
  IDeviceRegistrationLimit,
  IResult,
  Paging
} from '@c8y/client';
import { get, pick } from 'lodash-es';
import { BehaviorSubject, forkJoin, from, Observable, Subject } from 'rxjs';
import { AlertService, gettext, IRealtimeDeviceBootstrap } from '@c8y/ngx-components';
import { finalize, map, mergeMap, takeLast, takeUntil } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class RegisterDeviceService {
  readonly _loading: Subject<boolean> = new Subject();
  readonly _limit: BehaviorSubject<IDeviceRegistrationLimit> = new BehaviorSubject({
    isReached: false
  });
  readonly _deviceRegistrationRequests: BehaviorSubject<{
    data: IDeviceRegistration[];
    paging?: Paging<IDeviceRegistration>;
  }> = new BehaviorSubject({ data: [] });
  readonly deviceRegistrationRequests$: Observable<{
    data: IDeviceRegistration[];
    paging?: Paging<IDeviceRegistration>;
  }> = this._deviceRegistrationRequests.asObservable();
  readonly loading$: Observable<boolean> = this._loading.asObservable();
  readonly limit$: Observable<IDeviceRegistrationLimit> = this._limit.asObservable();
  paging: Paging<IDeviceRegistration>;

  private readonly deviceRegUrl = '/deviceregistration';
  private endSubscriptions: Subject<void> = new Subject();

  constructor(
    private router: Router,
    private deviceRegService: DeviceRegistrationService,
    private alertService: AlertService
  ) {}

  isDeviceRegistration(): boolean {
    return get(this.router, 'url') === this.deviceRegUrl;
  }

  internalListUpdate(
    deviceRequests: IDeviceRegistration[],
    pagingObject?: Paging<IDeviceRegistration>
  ) {
    let { paging, data } = this._deviceRegistrationRequests.getValue();
    if (pagingObject) {
      paging = pagingObject;
    }
    data = [...data, ...deviceRequests];
    this._deviceRegistrationRequests.next({ data, paging });
  }

  onDeviceBootstrap(bsData: IRealtimeDeviceBootstrap) {
    const { id, status } = bsData;
    this._deviceRegistrationRequests.next({
      data: this.updateStatusById(id, status)
    });
  }

  list(pageSize = 100) {
    this._loading.next(true);
    this._deviceRegistrationRequests.next({ data: [], paging: undefined });

    from(this.deviceRegService.list({ pageSize, withTotalPages: true }))
      .pipe(
        takeUntil(this.endSubscriptions),
        finalize(() => this.limit())
      )
      .subscribe(
        res => {
          const { data, paging } = res;
          this.internalListUpdate(data, paging);
          this._loading.next(false);
        },
        err => {
          this._loading.next(false);
          this.alertService.addServerFailure(err);
        }
      );
  }

  createMultiple(newDeviceRequests: IDeviceRegistrationCreate[]) {
    if (newDeviceRequests && newDeviceRequests.length > 0) {
      this._loading.next(true);
      const newRequests$ = newDeviceRequests.map(element => {
        return from(
          this.deviceRegService.create(element).catch((err: IResult<IDeviceRegistration>) => ({
            res: err.res,
            data: { ...err.data, id: element.id }
          }))
        );
      });

      const groupedRequests: {
        success: IDeviceRegistration[];
        failed: IDeviceRegistration[];
      } = {
        success: [],
        failed: []
      };

      return forkJoin(newRequests$).pipe(
        mergeMap(resp =>
          resp.map(el => {
            el.res.ok
              ? groupedRequests.success.push(el.data)
              : groupedRequests.failed.push(el.data);
            return groupedRequests;
          })
        ),
        takeLast(1),
        finalize(() => {
          this.internalListUpdate(groupedRequests.success);
          this._loading.next(false);
        })
      );
    }
  }

  remove(id: string) {
    this._loading.next(true);
    from(this.deviceRegService.delete(id))
      .pipe(takeUntil(this.endSubscriptions))
      .subscribe(
        () => {
          this._deviceRegistrationRequests.next({
            data: this.removeDeviceRegistrationRequestById(id)
          });
          this._loading.next(false);
          this.alertService.success(gettext('Device registration cancelled.'));
        },
        err => {
          this._loading.next(false);
          this.alertService.addServerFailure(err);
        }
      );
  }

  accept(request: IDeviceRegistration) {
    this._loading.next(true);
    const payload = pick(request, ['id', 'securityToken']);
    from(this.deviceRegService.accept(payload))
      .pipe(takeUntil(this.endSubscriptions))
      .subscribe(
        () => {
          this._deviceRegistrationRequests.next({
            data: this.updateStatusById(payload.id, DeviceRegistrationStatus.ACCEPTED)
          });
          this.limit();
          this._loading.next(false);
          this.alertService.success(gettext('Device registration accepted.'));
        },
        err => {
          this._loading.next(false);
          this.alertService.addServerFailure(err);
        }
      );
  }

  acceptAll() {
    const acceptedDeviceRequests: IDeviceRegistrationAccept[] = [];
    const failedDeviceRequests: IDeviceRegistrationAccept[] = [];
    this._loading.next(true);

    from(this.deviceRegService.acceptAll())
      .pipe(
        takeUntil(this.endSubscriptions),
        map(({ data }) => {
          data.map(deviceRegistrationRequest => {
            if (deviceRegistrationRequest.successful) {
              acceptedDeviceRequests.push(deviceRegistrationRequest);
              this.updateStatusById(
                deviceRegistrationRequest.id,
                DeviceRegistrationStatus.ACCEPTED
              );
            } else {
              failedDeviceRequests.push(deviceRegistrationRequest);
            }
          });
          return data;
        }),
        finalize(() => {
          // update rendered list with successful accepted device registrations
          // see: this.updateStatusById(...)
          this.internalListUpdate([]);
          this.limit();
          this._loading.next(false);
          if (failedDeviceRequests.length > 0) {
            this.alertService.warning(
              gettext('Could not accept all pending registration requests.'),
              JSON.stringify(
                {
                  failedDeviceRequests,
                  acceptedDeviceRequests
                },
                undefined,
                2
              )
            );
          } else {
            this.alertService.success(gettext('Accepted all pending registration requests.'));
          }
        })
      )
      .subscribe(
        () => {
          // empty by design
        },
        err => {
          this._loading.next(false);
          this.alertService.addServerFailure(err);
        }
      );
  }

  limit() {
    from(this.deviceRegService.limit())
      .pipe(takeUntil(this.endSubscriptions))
      .subscribe(
        res => this._limit.next(res.data),
        err => this.alertService.addServerFailure(err)
      );
  }

  getRequestByStatus(status: DeviceRegistrationStatus): IDeviceRegistration[] {
    return this._deviceRegistrationRequests.getValue().data.filter(req => req.status === status);
  }

  ngOnDestroy(): void {
    this.endSubscriptions.next();
    this.endSubscriptions.complete();
  }

  private updateStatusById(id: string, status: DeviceRegistrationStatus) {
    const items = this._deviceRegistrationRequests.getValue().data;
    const matchingElementIndex = items.findIndex(element => element.id === id);
    if (matchingElementIndex >= 0) {
      items[matchingElementIndex].status = status;
    }
    return items;
  }

  private removeDeviceRegistrationRequestById(id: string) {
    const items = this._deviceRegistrationRequests.getValue().data;
    const matchingElementIndex = items.findIndex(element => element.id === id);
    if (matchingElementIndex >= 0) {
      items.splice(matchingElementIndex, 1);
    }
    this._loading.next(false);
    return items;
  }
}
