import {
  AfterViewInit,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  OnDestroy
} from '@angular/core';
import { FormlyFieldConfig, FormlyFormOptions } from '@ngx-formly/core';
import {
  TenantUiService,
  gettext,
  C8yStepper,
  memoize,
  GainsightService
} from '@c8y/ngx-components';
import { FormControl, FormGroup } from '@angular/forms';
import { from, Observable, Subject, defer } from 'rxjs';
import { filter, shareReplay, switchMap, takeUntil, tap } from 'rxjs/operators';
import {
  IDeviceRegistration,
  IManagedObject,
  InventoryService,
  IResultList,
  ITenant,
  TenantService
} from '@c8y/client';
import { RegisterDeviceService } from '../register-device.service';
import { CdkStep } from '@angular/cdk/stepper';
import { BsModalRef } from 'ngx-bootstrap/modal';

interface GeneralDeviceRegistrationModelType {
  id: string;
  tenant?: { id: string };
  group?: { id: string; name?: string };
}

@Component({
  selector: 'c8y-general-device-registration',
  templateUrl: 'general-device-registration.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class GeneralDeviceRegistrationComponent implements AfterViewInit, OnDestroy {
  readonly MANAGEMENT = 'management';
  readonly FILTER: object = {
    withTotalPages: true,
    pageSize: 25
  };

  form = new FormGroup({});
  model = {
    devicesToCreate: [{} as GeneralDeviceRegistrationModelType]
  };
  options: FormlyFormOptions = {
    formState: {
      canLoadTenants: true
    }
  };

  PRODUCT_EXPERIENCE = {
    EVENT: 'deviceRegistration',
    COMPONENT: 'single-general-registration',
    RESULT: { SUCCESS: 'registrationSuccess', FAILURE: 'registrationFailure' }
  };

  isLoading$: Observable<boolean>;
  success: IDeviceRegistration[] = [];
  failed: IDeviceRegistration[] = [];

  fields: FormlyFieldConfig[] = [
    {
      type: 'array',
      key: 'devicesToCreate',
      templateOptions: {
        addText: gettext('Add device'),
        addTextDataCy: 'add-device'
      },
      fieldArray: {
        fieldGroup: [
          {
            key: 'id',
            type: 'string',
            focus: true,
            templateOptions: {
              placeholder: '0123ab32fcd',
              label: gettext('Device ID'),
              required: true
            },
            validators: {
              unique: {
                expression: (control: FormControl) => {
                  const found = (
                    control.root.get('devicesToCreate').value as Array<{ id: string }>
                  ).filter(el => el.id === control.value);
                  return found.length === 0;
                },
                message: () => gettext('Device ID duplicates are not allowed')
              }
            }
          },
          {
            key: 'tenant',
            type: 'typeahead',
            hideExpression: (model: any, formState: any, field: FormlyFieldConfig) => {
              if (!formState?.canLoadTenants) {
                field.formControl.setValue(null);
              }
              return !formState?.canLoadTenants || false;
            },
            defaultValue: { id: this.MANAGEMENT },
            templateOptions: {
              label: gettext('Add to tenant'),
              required: true,
              c8yForOptions: this.canLoadTenants$().pipe(
                filter(canLoad => canLoad),
                switchMap(() => this.getTenants$())
              ) as Observable<IResultList<ITenant>>,
              container: 'body',
              displayProperty: 'id',
              valueProperties: ['id']
            },
            hooks: {
              onInit: _field =>
                this.canLoadTenants$().pipe(
                  tap(canLoad => {
                    this.options.formState.canLoadTenants = canLoad;
                    this.cd.detectChanges();
                  })
                )
            }
          },
          {
            key: 'group',
            type: 'typeahead',
            expressionProperties: {
              'templateOptions.disabled': (
                model: any,
                formState: any,
                field: FormlyFieldConfig
              ) => {
                if (formState?.canLoadTenants) {
                  if (model?.tenant?.id !== this.MANAGEMENT) {
                    field.formControl.setValue(null);
                  }
                  return !(model?.tenant?.id === this.MANAGEMENT);
                }
                delete field?.templateOptions?.description;
                return false;
              }
            },
            templateOptions: {
              disabled: false,
              label: gettext('Add to group'),
              description: gettext(
                'You can add device to specific group for management tenant only.'
              ),
              container: 'body',
              displayProperty: 'name',
              valueProperties: ['id'],
              c8yForOptions: this.getGroups$()
            },
            hooks: {
              onInit: _field =>
                this.canLoadTenants$().pipe(
                  tap(canLoad => {
                    this.options.formState.canLoadTenants = canLoad;
                    this.cd.detectChanges();
                  })
                )
            }
          }
        ]
      }
    }
  ];

  private destroy$: Subject<any> = new Subject();
  private lastCreatedDevices: GeneralDeviceRegistrationModelType[] = [];

  constructor(
    private tenantUIService: TenantUiService,
    private tenantService: TenantService,
    private registerDeviceService: RegisterDeviceService,
    private inventoryService: InventoryService,
    private cd: ChangeDetectorRef,
    public bsModalRef: BsModalRef,
    private gainsightService: GainsightService
  ) {
    this.isLoading$ = this.registerDeviceService.loading$;
  }

  ngAfterViewInit() {
    this.cd.detectChanges();
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  create(eventObject: { stepper: C8yStepper; step: CdkStep }) {
    if (this.model?.devicesToCreate?.length > 0) {
      this.lastCreatedDevices = [...this.model.devicesToCreate];

      const dataToSend = this.model.devicesToCreate.map(
        (el: GeneralDeviceRegistrationModelType) => {
          const { id, tenant, group } = el;
          let data: { id: string; tenantId?: string; groupId?: string } = { id };

          if (tenant?.id) {
            data = { ...data, tenantId: tenant.id };
          }

          if (group?.id) {
            data = { ...data, groupId: group.id };
          }

          return data;
        }
      );

      this.registerDeviceService
        .createMultiple(dataToSend)
        .pipe(takeUntil(this.destroy$))
        .subscribe(requests => {
          this.success = requests.success;
          if (this.success.length > 0) {
            this.gainsightService.triggerEvent(this.PRODUCT_EXPERIENCE.EVENT, {
              result: this.PRODUCT_EXPERIENCE.RESULT.SUCCESS,
              component: this.PRODUCT_EXPERIENCE.COMPONENT
            });
          }

          this.failed = requests.failed;
          if (this.failed.length > 0) {
            this.gainsightService.triggerEvent(this.PRODUCT_EXPERIENCE.EVENT, {
              result: this.PRODUCT_EXPERIENCE.RESULT.FAILURE,
              component: this.PRODUCT_EXPERIENCE.COMPONENT
            });
          }

          if (eventObject) {
            eventObject.stepper.next();
          }
        });
    }
  }

  fixErrors(event: { stepper: C8yStepper; step: CdkStep }, failedRequests: IDeviceRegistration[]) {
    if (failedRequests && failedRequests.length > 0) {
      this.options.resetModel({
        devicesToCreate: [
          ...this.lastCreatedDevices.filter(el =>
            failedRequests.map(data => data.id).includes(el.id)
          )
        ]
      });
      this.cd.detectChanges();
    }
    event?.stepper.previous();
  }

  @memoize()
  private canLoadTenants$(): Observable<boolean> {
    return defer(() => from(this.tenantUIService.isManagementTenant())).pipe(shareReplay(1));
  }

  @memoize()
  private getTenants$(): Observable<IResultList<ITenant>> {
    return defer(() => from(this.tenantService.list(this.FILTER))).pipe(shareReplay(1));
  }

  @memoize()
  private getGroups$(): Observable<IResultList<IManagedObject>> {
    return defer(() =>
      from(
        this.inventoryService.listQuery(
          { __filter: { __has: 'c8y_IsDeviceGroup' }, __orderby: [{ name: 1 }] },
          { ...this.FILTER }
        )
      )
    ).pipe(shareReplay(1));
  }
}
